import './App.css';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Navbar from './componets/Navbar';
import News from './componets/News';
import { useState } from 'react';
import LoadingBar from 'react-top-loading-bar'
import Visual from './componets/Visual';
import Dashboard from './Dashboard';


function App() {
  let api = process.env.REACT_APP_NEWS_API
  const country = "in";
  const pageSize = 12;
  const [page, setPage] = useState(1);
  const [progress, setProgress] = useState(0);
  const [articles, setArticles] = useState([]);
  return (
    <div className="App">
      <Router>
        {/* <LoadingBar color='#f11946' progress={progress} /> */}
        <Navbar setPage={setPage} />
        {/* <Visual articles={articles} />   */}
        <Dashboard/>
        <Routes>
          {/* <Route exact path='/' element={<Dashboard />} /> */}
          <Route exact path='/economic' element={<News articles={articles} setArticles={setArticles} setProgress={setProgress} setPage={setPage} key="business" api={api} country={country} pageSize={pageSize} page={page} category={"Economic"} />} />

          <Route exact path='/industrial' element={<News articles={articles} setArticles={setArticles} setProgress={setProgress} setPage={setPage} key="industrial" api={api} country={country} pageSize={pageSize} page={page} category={"Industries"} />} />

          <Route exact path='/environmental' element={<News articles={articles} setArticles={setArticles} setProgress={setProgress} setPage={setPage} key="environmental" api={api} country={country} pageSize={pageSize} page={page} category={"Environmental"} />} />

          <Route exact path='/' element={<News articles={articles} setArticles={setArticles} setProgress={setProgress} setPage={setPage} key="general" api={api} country={country} pageSize={pageSize} page={page} category={""} />} />

          <Route exact path='/political' element={<News articles={articles} setArticles={setArticles} setProgress={setProgress} setPage={setPage} key="political" api={api} country={country} pageSize={pageSize} page={page} category={"Political"} />} />

          <Route exact path='/technology' element={<News articles={articles} setArticles={setArticles} setProgress={setProgress} setPage={setPage} key="technology" api={api} country={country} pageSize={pageSize} page={page} category={"Technological"} />} />

          <Route exact path='/social' element={<News articles={articles} setArticles={setArticles} setProgress={setProgress} setPage={setPage} key="social" api={api} country={country} pageSize={pageSize} page={page} category={"Social"} />} />

          <Route exact path='/health' element={<News articles={articles} setArticles={setArticles} setProgress={setProgress} setPage={setPage} key="health" api={api} country={country} pageSize={pageSize} page={page} category={"Healthcare"} />} />

        </Routes>
      </Router>

    </div>
  );
}

export default App;
