import React, { useEffect, useState } from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner';
import InfiniteScroll from 'react-infinite-scroll-component';

const News = (props) => {
    const { setProgress, setArticles, articles } = props;
    const [totalResults, setTotalResults] = useState(0);
    const [loading, setLoading] = useState(true);

    const fetchApi = async (page = 1) => {
        setLoading(true);
        setArticles([]);
        setProgress(10);
        // let url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.api}&page=${page}&pagesize=${props.pageSize}`;
        let url = `http://localhost:5000/api/data?page=${page}&pageSize=${props.pageSize}&category=${props.category}`;
        // let url='http://localhost:5000/api/data?page=1&pageSize=10'


        setProgress(30);
        let res = await fetch(url)
        setProgress(50);
        const json = await res.json()
        setProgress(70);
        setArticles(json.articles);
        setTotalResults(json.totalResults)
        setLoading(false);
        setProgress(100);
    }

    const fetchData = async () => {
        // let url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.api}&page=${props.page + 1}&pagesize=${props.pageSize}`;
        let url = `http://localhost:5000/api/data?page=${props.page+1}&pageSize=${props.pageSize}&category=${props.category}`;
        // let url='http://localhost:5000/api/data?page=1&pageSize=10'

        let res = await fetch(url)
        const json = await res.json()
        setArticles(articles.concat(json.articles));
        props.setPage(props.page + 1);
        setTotalResults(json.totalResults)
    }

    useEffect(() => {
        fetchApi();
        // eslint-disable-next-line
    }, [])
    return (
        <div className='container my-3'>
            <h1 style={{ marginTop: "4rem" }}
                className='text-center'>Breaking News</h1>
            {loading && <Spinner />}

            <InfiniteScroll
                dataLength={articles.length} //This is important field to render the next data
                next={fetchData}
                hasMore={props.page < totalResults / props.pageSize}
                loader={<Spinner />}
            >

                <div className="container row">
                    {articles.map(element => {
                        return <div key={element.title} className="news col-md-4 my-3"><NewsItem element={element} /></div>
                    })
                    }
                </div>
            </InfiniteScroll>
        {/* <PageChange pageSize={props.pageSize} totalResults={totalResults} fetchApi={fetchApi} page={props.page} setPage={props.setPage} /> */}
        </div>
    )
}

export default News