import React from 'react'

const NewsItem = (props) => {
  const { element } = props;
  // console.log(props.img)
  return (
    <div className="card">
      <div style={{ display: "flex", position: "absolute", right: 0 }}>
        <span className="badge badge-pill badge-danger">★{element.likelihood}</span>
      </div>
      {/* <div style={{ display: "flex", position: "absolute", left: 0 }}>
        <span className="badge badge-pill badge-danger">★{element.intensity}</span>
      </div> */}
      <img style={{ height: "13rem" }} src={!element.urlToImage ? "https://media.istockphoto.com/vectors/breaking-news-with-world-map-background-vector-vector-id1369150014?k=20&m=1369150014&s=612x612&w=0&h=8Zmzesd7lnHTMNCKfv9BZWCrc3BMVweDR7908TK_FtE=" : element.url} className="card-img-top" alt="..." />
      <div className="card-body">
        <h5 style={{ height: "4.6rem", overflow: "hidden" }} className="title card-title">{element.title.slice(0, 80)}...</h5>
        {/* <p style={element.description ? { height: "8rem" } : {}} className="description card-text">{!element.description ? "..." : element.description.slice(0, 200)}...</p> */}
        <p className="card-text"><small className='text-muted'>By {!element.source ? "Unknown" : element.source.slice(0, 15)} on {new Date(element.published).toGMTString()}</small></p>
        <a href={element.url} target="_blank" rel="noreferrer" className="btn btn-primary">Read More</a>
      </div>
    </div>
  )
}

export default NewsItem