import React from 'react'
import { Link } from 'react-router-dom';

const Navbar = (props) => {
    const { setPage } = props;

    const handleSearch = () => {
        const searchText = document.getElementById('searchBox').value.toLowerCase();
        const news = document.getElementsByClassName('news');
        for (const element of news) {
            let title = element.getElementsByClassName('title')[0].innerText.toLowerCase();
            let description = element.getElementsByClassName('description')[0].innerText.toLowerCase();

            if (title.includes(searchText) || description.includes(searchText)) {
                element.style.display = "block"
            }
            else
                element.style.display = "none";
        }

    }
    return (
        <nav className="fixed-top navbar navbar-expand-lg navbar-dark bg-dark">
            <Link className="navbar-brand" to="/">News</Link>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>

            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav mr-auto">
                    <li className="nav-item active">
                        <Link className="nav-link" to="/">Home</Link>
                    </li>
                    <li className="nav-item active">
                        <Link className="nav-link" to="/about">About</Link>
                    </li>
                </ul>

                <ul className="navbar-nav mr-auto">
                    <li className="nav-item"><Link onClick={() => setPage(1)} className="nav-link" to="/">General</Link></li>
                    <li className="nav-item"><Link onClick={() => setPage(1)} className="nav-link" to="/economic">Economic</Link></li>
                    <li className="nav-item"><Link onClick={() => setPage(1)} className="nav-link" to="/industrial">Industrial</Link></li>
                    <li className="nav-item"><Link onClick={() => setPage(1)} className="nav-link" to="/environmental">Environmental</Link></li>
                    <li className="nav-item"><Link onClick={() => setPage(1)} className="nav-link" to="/political">Political</Link></li>
                    <li className="nav-item"><Link onClick={() => setPage(1)} className="nav-link" to="/social">Social</Link></li>
                    <li className="nav-item"><Link onClick={() => setPage(1)} className="nav-link" to="/health">Health</Link></li>
                    <li className="nav-item"><Link onClick={() => setPage(1)} className="nav-link" to="/technology">Technology</Link></li>
                </ul>

                <form className="form-inline my-2 my-lg-0">
                    <input id="searchBox" onInput={handleSearch} className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" />
                    <button className="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
                </form>
            </div>
        </nav>
    )
}

export default Navbar