import ChartsEmbedSDK from "@mongodb-js/charts-embed-dom";
import React, { useEffect, useRef, useState } from 'react';

const Dashboard = () => {
    const sdk = new ChartsEmbedSDK({ baseUrl: 'https://charts.mongodb.com/charts-project-0-ejcdt' });

    const dashboard = sdk.createDashboard({
        dashboardId: "9e588dac-023c-4d86-9207-ca6a6b4d3158",
        height: "270vh",
        width: "110vw"
    });

    dashboard.render(document.getElementById('dashboard'))
    //   .catch(() => window.alert('Dashboard failed to initialise'));
    return (<>
        <div className='container my-3'>
            <h1 style={{ textAlign: "center", marginTop: "4rem" }}>Dashboard</h1>
        </div>
        <div >
            {/* <h2>fttf</h2> */}
            <div id="dashboard"></div>
        </div>
    </>
    )
}

export default Dashboard